import React from 'react'
import { Button } from 'react-bootstrap';
import watchesData from '../data/watchesData';
const AdminPage = () => {
    return (
        <section className='adminPage text-center'>

            <table className='w-100'>
                <thead>
                    <tr>
                        <th>#</th>
                        <th>Image</th>
                        <th>Title</th>
                        <th>Price</th>
                        <th>Edit</th>
                        <th>Delete</th>
                    </tr>
                </thead>

                

                <tbody>

                {watchesData.map((item)=>{
                    return(
                        <tr>
                            <td>{item.id}</td>
                            <td><img src={item.img} alt="" style={{width:"50px"}} /></td>
                            <td>{item.title}</td>
                            <td>{item.price}</td>
                            <td><Button variant='warning'>Edit</Button></td>
                            <td><Button variant='danger'>Delete</Button></td>
                        </tr>
                    )
                })}

                </tbody>
            </table>

        </section>

    )
}

export default AdminPage