import React from 'react'
import { Button, Col, Container, Row } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';
import { useParams } from 'react-router-dom'
import { useCart } from 'react-use-cart';
import watchesData from '../data/watchesData';

const ProductDetails = () => {
    const { url } = useParams();
    const productDetails = watchesData.find(p => p.id == url);
    const {addItem} = useCart();
    return (
        <section className='watchDetails'>
            <Container>

                <Row className='mt-3 g-5'>
                    <Col xs={12} sm={6} md={6}>
                        <img src={productDetails.img} alt="" className='w-100' />
                    </Col>

                    <Col xs={12} sm={6} md={6}>

                        <div className="rightMain">

                            <div className="featureBox">
                                <h3>Title:</h3>
                                <p>{productDetails.title}</p>
                            </div>

                            <div className="featureBox">
                                <h3>Price:</h3>
                                <p>${productDetails.price}</p>
                            </div>

                            <div className="featureBox">
                                <h3>Rate:</h3>
                                <p className='stars'>
                                    <i className="fa-solid fa-star"></i>
                                    <i className="fa-solid fa-star"></i>
                                    <i className="fa-solid fa-star"></i>
                                    <i className="fa-solid fa-star"></i>
                                    <i className="fa-solid fa-star"></i>
                                </p>
                            </div>

                            <div className="addToCart">
                                <Button variant='warning text-white' onClick={()=>{addItem(productDetails)}}>Add to cart</Button>
                            </div>
                        </div>
                    </Col>
                </Row>
            </Container>
            <div className="text-center">
                <LinkContainer to='/shop'>
                    <Button variant='danger' className='px-5 mt-5'>Go Back</Button>
                </LinkContainer>
            </div>

        </section>
    )
}

export default ProductDetails