import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { LinkContainer } from 'react-router-bootstrap';
import { useCart } from 'react-use-cart';
const Header = () => {
    const { items } = useCart();
    return (
        <header>
            <Navbar className='navbar-dark' expand="lg">
                <Container>
                    <Navbar.Brand href="/">
                        <img src="https://cdn.shopify.com/s/files/1/0559/0505/8894/files/headerlogo_a4e1978f-8eb8-422d-b58e-91b9b5f50ba4.png?v=1655190450" alt="" />
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="me-auto">

                            <div className="popUp-link" id='homeLink'>
                                <LinkContainer to='/home'>
                                    <Nav.Link>
                                        Home <i className="fa-solid fa-chevron-down"></i>
                                    </Nav.Link>
                                </LinkContainer>

                                <div className="linkHover">
                                    <ul>
                                        <li><a href="#">Home-1</a></li>
                                        <li><a href="#">Home-2</a></li>
                                        <li><a href="#">Home-3</a></li>
                                    </ul>
                                </div>
                            </div>

                            <LinkContainer to='/shop'>
                                <Nav.Link>Shop</Nav.Link>
                            </LinkContainer>

                            <LinkContainer to='/about'>
                                <Nav.Link>About</Nav.Link>
                            </LinkContainer>

                            <div className="popUp-link">
                                <LinkContainer to='/pages'>
                                    <Nav.Link>
                                        Pages <i className="fa-solid fa-chevron-down"></i>
                                    </Nav.Link>
                                </LinkContainer>

                                <div className="linkHover">
                                    <ul>
                                        <li><a href="#">Faq</a></li>
                                        <li><a href="#">Blog</a></li>
                                        <li><a href="#">Blog Detail</a></li>
                                        <li><a href="#">Product Detail</a></li>
                                    </ul>
                                </div>
                            </div>

                            <LinkContainer to='/contact'>
                                <Nav.Link>Contact</Nav.Link>
                            </LinkContainer>

                        </Nav>

                        <div className="navbar-end">

                            <LinkContainer to='/search'>
                                <button><i className="fa-solid fa-magnifying-glass"></i></button>
                            </LinkContainer>

                            <LinkContainer to='/cart'>
                                <button>
                                    <i className="fa-solid fa-cart-shopping"></i>
                                    <div className="cartInfo">
                                        <span>{items.length}</span>
                                    </div>
                                </button>
                            </LinkContainer>

                            <LinkContainer to="/admin">
                                <button><i className="fa-solid fa-user-tie"></i></button>
                            </LinkContainer>
                        </div>

                    </Navbar.Collapse>
                </Container>
            </Navbar>
        </header>
    )
}

export default Header