import React from 'react'
import watchesData from '../data/watchesData'
import SingleCard from './SingleCard'
const CardList = () => {
    return (
        <>
            {
                watchesData.map(item => {
                    return (
                        <SingleCard
                            allData={item}
                            key={item.id}
                            img={item.img}
                            title={item.title}
                            price={item.price}
                            id={item.id}
                        />
                    )
                })
            }
        </>
    )
}

export default CardList