import React, { useEffect, useState } from 'react'
import { Button } from 'react-bootstrap';
import { useCart } from 'react-use-cart'

const Cart = () => {
    const { items, updateItemQuantity } = useCart();
    const [cartLength, setCartLength] = useState("");
    useEffect(() => {
        if (items.length == 0) {
            setCartLength("block");
        } else {
            setCartLength("none");
        }
    });
    return (
        <div className='cartBody py-5 my-5 container'>

            <h2 className={`my-5 text-center d-${cartLength}`}>You haven't any item in the cart</h2>
            <table className='w-100'>
                <thead>
                    <tr>
                        <th>Image</th>
                        <th>Title</th>
                        <th>Price</th>
                        <th>Quantity</th>
                        <th>Total Price ($)</th>
                    </tr>
                </thead>



                <tbody>

                    {
                        items.map((item) => {
                            return (
                                <tr>
                                    <td><img src={item.img} alt="" style={{ width: "60px" }} /></td>
                                    <td>{item.title}</td>
                                    <td>${item.price}</td>
                                    <td>
                                        <Button variant='warning' onClick={() => updateItemQuantity(item.id, item.quantity - 1)}>
                                            -
                                        </Button>
                                        <span className='px-2'>{item.quantity}</span>
                                        <Button variant='warning' onClick={() => updateItemQuantity(item.id, item.quantity + 1)}>
                                            +
                                        </Button>
                                    </td>
                                    <td><p>${item.itemTotal}</p></td>
                                </tr>
                            )
                        })}

                </tbody>
            </table>
        </div>
    )
}

export default Cart