import React from 'react'

const Footer = () => {
  return (
    <footer>
        <div className="footerUp">
            <div className="container-fluid">
                <div className="row g-5">
                    <div className="col-12 col-sm-12 col-md-4">
                        <img src="https://cdn.shopify.com/s/files/1/0559/0505/8894/files/footerlogo_4de611ed-8eac-4471-abfd-22908314f987_x200@2x.png?v=16551910376" alt="" />
                    </div>

                    <div className="col-12 col-sm-12 col-md-4 list">
                        <ul>
                            <li className='list-head'>Quick Links</li>
                            <li><a href="#">Our Store</a></li>
                            <li><a href="#">Store Map</a></li>
                            <li><a href="#">Return Policy</a></li>
                            <li><a href="#">Cancellation</a></li>
                        </ul>
                    </div>

                    <div className="col-12 col-sm-12 col-md-4 list">
                        <ul className='text-center'>
                            <li>No: 58 A, East Madison <br /> Street, Baltimore, MD, USA <br /> 4508</li>
                            <li>Mail: <a href="#">imail@example.com</a></li>
                            <li>Phone: 0000 - 123 - 456789</li>
                        </ul>
                    </div>
                </div>

                <hr />

                <div className="footerDown">
                    <div className="row">
                        <div className="col-12 col-sm-6 col-md-8">
                            <p>© {new Date().getFullYear()} dt-dark fashion Design Themes</p>
                        </div>

                        <div className="col-12 col-sm-6 col-md-4">
                            <img className='w-100' src="https://demo2wpopal.b-cdn.net/freshio/wp-content/uploads/2020/08/payment_1.png" alt="" />
                        </div>
                    </div>
                </div>


            </div>
        </div>
    </footer>
  )
}

export default Footer