import React, { useRef, useState } from 'react'
import SingleCard from '../card/SingleCard';
import watchesData from '../data/watchesData';

const Search = () => {
    const [search,setSeacrh] = useState();
    const searchRef = useRef();
    return (
        <section className='searchSection'>

            <div className="row mb-5">
                <div className="col-12 col-sm-12 col-md-9" id='mainInput'>
                    <div className="mainBox">
                        <input type="text" name="" id="email" placeholder='Search...' onChange={e=>setSeacrh(e.target.value)} />
                    </div>
                </div>
            </div>

            <div className="resultMain">
                <div className="row g-5">
                    {
                        watchesData.filter(cd=>cd.title.toLocaleLowerCase().includes(search)).map(item => {
                            return (
                                <SingleCard
                                    allData={item}
                                    key={item.id}
                                    img={item.img}
                                    title={item.title}
                                    price={item.price}
                                    id={item.id}
                                />
                            )
                        })
                    }
                </div>
            </div>

        </section>
    )
}

export default Search